import anImage from '@/components/an-image/an-image.vue'
import {config} from '@/common/utils/congfig.js'
import {IndexModel} from "../../model";
const  indexModel = new IndexModel()

export default {
    name: "cosmetology",
    components:{
        anImage
    },
    data(){
        return {
            no_pic: getApp().globalData.no_pic,
            allChecked:false,
            sumPrice:"0.00",
            list:[],
            sumData:[],
            pages:{
                page:1,
                size:10
            }
        }
    },
    methods:{
        //全选
        radioChangeAll(){
            this.allChecked = !this.allChecked
            let list = this.list
            list.map((item)=>{
                return item.checked = this.allChecked
            })
            // 计算金额
            this._getPrice(list)
            this.list = JSON.parse(JSON.stringify(list))
        },
        order_buy(){
            if (this.sumData.length <= 0) {
                uni.showToast({
                    title : '请选择服务',
                    icon : 'none'
                })
                return false;
            }
            let data = JSON.stringify(this.sumData)
            uni.navigateTo({
                url:"/pagesB/car_wash_buyOrder/car_wash_buyOrder?data="+ data
            })
        },
        _getCosmetology(data){
            indexModel.getCosmetology(data).then((res) => {
                console.log(res.data)
                if(res.data) {
                    this.list = res.data.data
                }
            })
        },
        //计算价格
        _getPrice(data){
            let price = 0
            let sumData = []
            for (let item of data) {
                if(item.checked) {
                    let items = {
                        id:item.one_sku.id,
                        num:1
                    }
                    sumData.push(items)
                    price+=parseFloat(item.min_price)
                }
            }
            this.sumData = sumData
            this.sumPrice = price.toFixed(2)
        },
        radioChange(ids){
            let list = this.list
            list.map((item)=>{
                if(item.id == ids.id) {
                    item.checked = !item.checked
                }
            })
            this._getPrice(list)
            this.list = JSON.parse(JSON.stringify(list))
        }
    },
    onLoad(options){
        // console.log(options)
        // uni.showLoading({
        //     title:'loading...',
        //     mask:true
        // })
        
        uni.setNavigationBarTitle({
            title:options.title
        })
        let service_classify_id = options.service_classify_id
        let data = this.pages
        data.service_classify_id = service_classify_id
        this._getCosmetology(data)
    },
}
